body, html {
    height: 100%;
    margin: 0;
    /* display: flex; */
    /* justify-content: center;
    align-items: center; */
}

canvas{
    position: fixed;
    top:0;
    left:0;
}

@media(min-width: 850px){
    .guide{
        position: absolute; /* Make sure it's positioned over the canvas */
        z-index: 10; /* Higher z-index ensures it's above other content */
        display: flex;
        justify-content: left;
        align-items: left;
        text-align: left;
        width: auto; /* Adjust as needed */
        top: 5px; /* Distance from the top of the viewport */
        color: white;
        
    }
    .upper-right{
        justify-content: bottom;
        margin-right: 30px;
        font-size: 1.5rem;
        color: white;
    }
}
@media(max-width: 850px){
    .guide{
        position: absolute; /* Make sure it's positioned over the canvas */
        z-index: 10; /* Higher z-index ensures it's above other content */
        display: flex;
        justify-content: left;
        align-items: left;
        text-align: left;
        width: auto; /* Adjust as needed */
        top: 10px; /* Distance from the top of the viewport */
        opacity: 0%;
        color: white;
    }
    .upper-right{
        display: flex;
        width: 100vw;
        justify-content: center;
        text-align: center;
        margin-right: 30px;
        font-size: 1rem;
        color: white;
    }
}


.console {
    z-index: 10; 
    display: flex;
    flex-basis: 10px;
    flex-direction: column;
    align-items: flex-end;
    position: absolute;
    justify-content: center;
    bottom: 50px;
    right: 15px;
}

.output {
    position: absolute;
    z-index: 10;
    top: 45vh;
    left: 40vw;
    opacity: 50%;
}
#console_log{
    z-index: 10;
}

.stats-div{
    z-index: 10; 
    position: fixed;
    width: 100vw;
    
}

.stats{
    display:flex;
    justify-content: flex-end;
    margin-top: 30px;
    margin-right: 20px;
}



.grid-console{
    position: absolute;
    right: 10px;
    bottom: 10px;
    display: grid;
    grid-template-rows: repeat( 3, 67px);
	grid-template-columns: repeat(3, 122px);
    z-index: 10;
}

#purchase-title{
    position: absolute;
    right: 252px;
    bottom: 185px;
    border-radius: 5px;
    background-color: black;
    outline: none;
    border: none;
    width: 121px;
    height: 29px;
    font-size: 20px;
    color: white;
    font-weight: 600;
    padding-top:3px;
    margin-top: 5px;
    margin-bottom: 3px;
    justify-content: center;
    text-align: center;
}

.purchase {
    border-radius: 5px;
    background-color: black;
    outline: none;
    border: none;
    width: 120px;
    height: 65px;
    font-size: 12px;
    color: white;
    font-weight: 600;
    cursor: pointer; 
    margin-top: 3px;
    margin-bottom: 3px;
    opacity: 0%;
} 
.grid-text{
    position: absolute;
    right: 10px;
    bottom: 10px;
    display: grid;
    grid-template-rows: repeat( 3, 60px);
	grid-template-columns: repeat(3, 121px);
    z-index: 9;
}

.champ-description{
    border-radius: 5px;
    background-color: black;
    outline: none;
    border: none;
    width: 115px;
    height: 55px;
    font-size: 12px;
    color: white;
    font-weight: 600;
    cursor: pointer; 
    margin-top: 3px;
    margin-bottom: 3px;

    /* text-align: center; */
    padding-left: 5px;
    padding-top: 4px;
    display: grid;
    grid-template-columns: 1fr 1fr; 
    grid-template-rows: 1fr 1fr 1fr;
    
    
}

.champ-name{
    padding:0%;
    padding-left: 0%;
    font-size: 12px;
    color: white;  
    grid-area: 1 / 1 / 4 / 2;
}
.champ-img{
    border-radius: 5%;
}
.t1{
    padding:0%;
    padding-left: 2%;
    font-size: 11px;
    color: white;
    grid-area: 1 / 2 / 2 / 3;
}
.t2{
    padding:0%;
    padding-left: 2%;
    font-size: 11px;
    color: white;
    grid-area: 2 / 2 / 3 / 3
}
.t3{
    padding:0%;
    padding-left: 2%;
    font-size: 11px;
    color: white;
    grid-area: 3 / 2 / 4 / 3
}

.purchase {
    border-radius: 5px;
    background-color: black;
    outline: none;
    border: none;
    width: 120px;
    height: 65px;
    font-size: 12px;
    color: white;
    font-weight: 600;
    cursor: pointer; 
    margin-top: 3px;
    margin-bottom: 3px;
} 


.world-control{
    display: flex;
    flex-direction: column;
    position: absolute;
    bottom: 150px;
    left: 10px;
    text-align: center;
    border-radius: 5px;
    outline: none;
    border: none;
    width: 65px;
    height: 35px;
    font-size: 12px;
    /* background-color: white; */
    font-weight: 600;
    cursor: pointer; 
    margin-top: 3px;
    margin-bottom: 3px;
    padding-top: 8px;
    z-index: 10;
}
#music{
    border-radius: 5%;
    background-color: white;
    font-weight: 1200;
    font-size: 25px;        
    margin-top: 3px;
    margin-bottom: 3px;
    padding-top: 7px;
    padding-bottom: 6px;
}

#pause{

        border-radius: 5%;
        background-color: white;
        font-weight: 1200;
        font-size: 35px;        
        margin-top: 3px;
        margin-bottom: 3px;
        padding-top: 0px;
        padding-bottom: 0px;
    }
    
#skip{
    border-radius: 5%;
    background-color: white;
    font-weight: 600;
    padding-bottom: 10px;
    margin-top: 3px;
    margin-bottom: 3px;
    padding-top: 10px;
}

#pop-up{
    /* z-index: 1000; */
    width: 0%;
    height: 0%;
    background-color: none;
    display: flex;
    align-items: center;
    text-align: center; 
}
#pop-up-dialog{
    z-index: 100;
    width: 360px;
    padding-top: 15px;
    padding-bottom: 25px;
    background: black;
    border-radius: 6px;
    position:absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    color: white;
    font-weight: 500;
    font-family:Arial, Helvetica, sans-serif;
    font-size: 1.1rem;
    
}

#pop-up-btn{
    background: none;
    border-radius: 4px;
    padding: 8px 0;
    width: 310px;
    font-size: 1rem;
    font-weight: 900;
    background: white;
    border: 0;
    outline: 0;
    cursor: pointer;
    /* box-shadow: 0 2px 10px rgba(e, 0, 0, 0.8); */
}
